import React, { useEffect, useRef, useState } from 'react'
import Background from '../../../components/shared/Ui/Background'
import animationData from '../../../../static/Images/WhyParentsLoveUs/data-comp-v1.json'
import profileImg from '../../../../static/Images/reviews/image.svg'
import stars from '../../../../static/Images/reviews/stars.svg'
import scribble from '../../../../static/Images/reviews/scribble.svg'
import bigstar from '../../../../static/Images/reviews/bigstar.avif'

import "../../../Styles/pages/reviews.css"
import "../../../Styles/pages/realfeedback.css"
const RealFeedback = () => {
      const contentRef = useRef(null);
       const [contentHeight, setContentHeight] = useState('100vh'); 
      useEffect(() => {
       const node = contentRef.current;
       if (!node) return;
     
       const updateHeight = () => {
         const height = node.getBoundingClientRect().height;
         setContentHeight(`${height + 500}px`);
       };
     
       const observer = new ResizeObserver(() => {
         updateHeight();
       });
     
       observer.observe(node);
     
       // Trigger once after timeout in case of image delay
       const timer = setTimeout(updateHeight, 500);
     
       return () => {
         observer.disconnect();
         clearTimeout(timer);
       };
     }, [contentRef]);
  return (
     <section className=' real-feedback-container' >
        <img src={bigstar} className="bigstar-feedback" alt="big star review feedback" />
 
  <Background bg="#FEAE37" height={contentHeight}  />
    <main className='feedback-main' >
        <div className="feedback-content">
            <span invertedDescription="true"  className='section-subtitle'>What Parents Are Saying</span>
        <h1 invertedText="true" className='section-title'>Real Feedback, Real Results</h1>
        <p invertedText="true" className="section-description">Our customers love the convenience, quality, and peace of mind we provide. From the ease of ordering to the excitement of receiving perfectly packed boxes, see why parents trust us to get their kids ready for school—stress-free.</p>
        </div>

        {/* reviews */}
<div className="feedback-cards-container container" ref={contentRef}>
    {
    [1,2,3,4,5,6].map((item) => (
      
        <div className='review-container'>
            <div className='review-content-image'>
                <img src={profileImg} alt="" className='review-content-img' />
            </div>
            <div className='review-content-text'>
                <div className='review-content'>
                    <span className="review-count-real-feedback">5.2</span>
                  <div >
                      <img src={stars} alt="" />
                  </div>
                 
                </div>
            </div>
              <div className='review-content-scribble'>
                 <h5 className='review-user-name'>Janette Williams</h5>
                 <p className="review-user-comment">Best back-to-school shopping experience! The box arrived on time, everything was organized, and it perfectly matched my child’s school supply list. Highly recommend this service!</p>
              <img src={scribble} alt="review scribble" className='scribble-image-review' />
              </div>
        </div>
    ))

}
</div>
    </main>
   </section>
    
)
}

export default RealFeedback