import React, { useRef, useEffect, useState } from 'react';
import { useInView } from 'react-intersection-observer'; // ⬅️ Import here
import "../../../Styles/pages/whyParentsLoveUs.css";
import Background from '../../../components/shared/Ui/Background';
import animationData from '../../../../static/Images/WhyParentsLoveUs/data-comp-v1.json';
import bottom_cloud from "../../../../static/Images/header/cloud-bottom.svg";
import books from "../../../../static/Images/WhyParentsLoveUs/books.svg";
import clock1 from "../../../../static/Images/WhyParentsLoveUs/clock1.svg";
import clock2 from "../../../../static/Images/WhyParentsLoveUs/clock2-v2.svg";
import reducestress from "../../../../static/Images/WhyParentsLoveUs/reducestress.svg";
import boost from "../../../../static/Images/WhyParentsLoveUs/boost.svg";
import convenience from "../../../../static/Images/WhyParentsLoveUs/convenience-v2.svg";
import reordering from "../../../../static/Images/WhyParentsLoveUs/reordering.svg";
import suppliesBooksJson from '../../../../static/lottie-magic-school-bus-books.json';

const BasicLottie = React.lazy(() => import('../BasicLottie/BasicLottie'));

function WhyParentsLoveUs() {
  const contentRef = useRef(null);
  const [contentHeight, setContentHeight] = useState('100vh');

  const { ref: lottieRef, inView: isLottieInView } = useInView({
    threshold: 0.3,
    triggerOnce: false,
     rootMargin: '-20% 0px 0px 0px', 
  });

  useEffect(() => {
    const node = contentRef.current;
    if (!node) return;

    const updateHeight = () => {
      const height = node.getBoundingClientRect().height;
      setContentHeight(`${height + 200}px`);
    };

    const observer = new ResizeObserver(() => updateHeight());
    observer.observe(node);
    const timer = setTimeout(updateHeight, 500);

    return () => {
      observer.disconnect();
      clearTimeout(timer);
    };
  }, [contentRef]);

  const whyParentsLoveUsData = [
    { id: 1, title: "Save Time", subtitle: "No more endless shopping trips or hunting down hard-to-find items.", image: clock1, width: 109.2, height: 137 },
    { id: 2, title: "Eliminate Guesswork", subtitle: "Supplies match each school's approved list exactly—no second-guessing.", image: clock2, width: 121.8, height: 165 },
    { id: 3, title: "Reduce Stress", subtitle: "Our curriculum is built to spark curiosity and keep children engaged.", image: reducestress, width: 103, height: 106 },
    { id: 4, title: "Boost Confidence", subtitle: "All our teachers are trained professionals passionate about early education.", image: boost, width: 94.01, height: 124.2 },
    { id: 5, title: "Convenience Delivered", subtitle: "We understand busy lives—our lessons fit around your schedule.", image: convenience, width: 119, height: 104.7 },
    { id: 6, title: "One-Click Reordering", subtitle: "Get quality education without breaking the bank.", image: reordering, width: 114, height: 127 },
  ];
  console.log(isLottieInView);

  return (
    <div className=' whyparentsloveus-container'>
      <div className='whyparentsloveus-bottom-cloud'>
        <img src={bottom_cloud} alt="" />
      </div>
    

      {/* ✅ Attach ref to Lottie container */}
      <div className='whyparentsloveus-books-image' ref={lottieRef}>
        <React.Suspense fallback={<div>Loading animation...</div>}>
          <BasicLottie 
            animationData={suppliesBooksJson} 
            height={700} 
            shouldPlay={isLottieInView} // ✅ Only play when in view
          />
        </React.Suspense>
      </div>

      <Background 
        bg="#0B3A4C" 
        height={contentHeight} 
        source={animationData} 
      /> 

      <section className='whyparentsloveus-content-section container'>
        <main className='whyparentsloveus-content-main' ref={contentRef}>
          <div className='whyparentsloveus-content-main-container'>
            <span className='section-subtitle'>Why Parents Love It</span>
            <h1 className='section-title' invertedtext="true">Smarter Shopping</h1>
            <p className="section-description">
              Save time, skip the crowds, and feel confident knowing your child has exactly what they need. 
              From pre-approved school lists to doorstep delivery and one-click reorders, our simple system 
              makes back-to-school shopping fast, stress-free, and completely foolproof.
            </p>
          </div>
          
          <div className='whyparentsloveus-items-container-container'>
            {whyParentsLoveUsData.map((item) => (
              <div className='whyparentsloveus-items-main-container' key={item.id}>
                <div className='whyparentsloveus-item-image'>
                  <img src={item.image} alt={item.title} height={item.height} width={item.width} />
                </div>
                <div>
                  <h1 className='whyparentsloveus-item-title'>{item.title}</h1>
                  <p className='whyparentsloveus-item-subtitle'>{item.subtitle}</p>
                </div>
              </div>
            ))}
          </div>
        </main>
      </section>
    </div>
  );
}

export default WhyParentsLoveUs;
