import React, { useEffect, useState, useMemo } from "react";
import "../../../Styles/pages/SchoolSearchPanel.css";
import VirtualizedSelect from "../../shared/Ui/VirtualizedSelect";

function SchoolSearchPanel() {
  const [schoolData, setSchoolData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [gradesData, setGradesData] = useState([
    { value: "K", label: "Kindergarten" },
    { value: "1", label: "Grade 1" },
    { value: "2", label: "Grade 2" },
    { value: "3", label: "Grade 3" },
    { value: "4", label: "Grade 4" },
    { value: "5", label: "Grade 5" },
    { value: "6", label: "Grade 6" },
    { value: "7", label: "Grade 7" },
    { value: "8", label: "Grade 8" },
    { value: "9", label: "Grade 9" },
    { value: "10", label: "Grade 10" },
    { value: "11", label: "Grade 11" },
    { value: "12", label: "Grade 12" }
  ])
  
  useEffect(() => {
    fetch("./us_schools.json")
      .then((response) => response.json())
      .then((data) => {
        setSchoolData(data);
        setIsLoading(false);
      })
      .catch((error) => {
        console.error("Error loading JSON:", error);
        setIsLoading(false);
      });
  }, []);

  const [form, setForm] = useState({
    state: "",
    school: "",
    teacher: "",
    grade:""
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSearch = () => {
    console.log("Searching with:", form);
    // Implement your search logic
  };

  // Get unique states
  const states = useMemo(() => {
    const uniqueStates = [...new Set(schoolData.map(item => item.State))];
    return uniqueStates.map(state => ({
      value: state,
      label: state
    }));
  }, [schoolData]);

  // Get schools filtered by selected state
  const schools = useMemo(() => {
    if (!form.state) return [];
    return schoolData
      .filter(item => item.State === form.state)
      .map(item => ({
        value: item["School Name"],
        label: item["School Name"]
      }));
  }, [schoolData, form.state]);

  // Get Grades filtered by selected school
  // const grades = useMemo(() => {
  //   if (!form.school) return [];
  //   return gradesData.filter(grade => grade.value === form.grade);
  // }
  // , [gradesData, form.school, form.grade]);

  return (
    <div className="home-school-panel-container">
      <div className="home-school-panel-inputs">
        <VirtualizedSelect
          label="State"
          name="state"
          labelText="State"
          value={form.state}
          onChange={handleChange}
          placeholder="Select your state"
          options={states}
          isLoading={isLoading}
        />

        <VirtualizedSelect
          label="School"
          name="school"
          labelText="School"
          value={form.school}
          onChange={handleChange}
          placeholder="Select your School"
          options={schools}
          isLoading={isLoading}
          isDisabled={!form.state}
        />

        <VirtualizedSelect
          label="Grade"
          name="grade"
          labelText="Grade"
          value={form.grade}
          onChange={handleChange}
          placeholder="Select your Grade"
          options={gradesData}
          isLoading={isLoading}
          isDisabled={!form.school}
        />

        {/* Search Button */}
        <div className="search-panel-inputs-items">
          <label htmlFor="button" className="search-label">Search</label>
          <button onClick={handleSearch} className="home-search-panel-button">
            Search
          </button>
        </div>

        {/* Help Text */}
        <p className="school-help-text">
          Don't See Your School or Teacher? No problem!{" "}
          <a href="/support" className="school-help-link">
            Click here
          </a>{" "}
          to find out how we can help!
        </p>
      </div>
    </div>
  );
}

export default SchoolSearchPanel;