import React, { useState, useMemo } from 'react';
import { FixedSizeList as List } from 'react-window';
import Select from 'react-select';

const VirtualizedSelect = ({
  label,
  name,
  value,
  onChange,
  placeholder,
  options = [],
  disabled = false,
  labelText = '',
}) => {
  const [inputValue, setInputValue] = useState('');
  const [menuIsOpen, setMenuIsOpen] = useState(false);

  // Filter options based on search input
  const filteredOptions = useMemo(() => {
    if (!inputValue) return options;
    return options.filter(option =>
      option.label.toLowerCase().includes(inputValue.toLowerCase())
    );
  }, [options, inputValue]);

  // Custom option renderer for react-window
  const Option = ({ index, style }) => (
    <div
      style={style}
      onClick={() => {
        onChange({ target: { name, value: filteredOptions[index].value } });
        setMenuIsOpen(false); // Close the menu when an option is selected
      }}
      className="virtualized-option"
    >
      {filteredOptions[index].label}
    </div>
  );

  return (
    <div className={`search-panel-inputs-items`}>
      <label htmlFor={name} className="input-label">
        {labelText}
      </label>
      
      <Select
        className="home-search-panel-input"
        inputValue={inputValue}
        onInputChange={setInputValue}
        value={filteredOptions.find(opt => opt.value === value) || null}
        onChange={(selected) => {
          onChange({ target: { name, value: selected?.value || '' } });
          setMenuIsOpen(false); // Close the menu when an option is selected
        }}
        placeholder={placeholder}
        isMulti={false}
        isDisabled={disabled}
        menuIsOpen={menuIsOpen}
        onMenuOpen={() => setMenuIsOpen(true)}
        onMenuClose={() => setMenuIsOpen(false)}
        components={{
          MenuList: ({ children, ...props }) => (
            <List
              height={Math.min(300, filteredOptions.length * 35)}
              itemCount={filteredOptions.length}
              itemSize={35}
              width="100%"
            >
              {Option}
            </List>
          ),
        }}
        options={filteredOptions}
        styles={{
          control: (provided, state) => ({
            ...provided,
            minHeight: '40px',
            height: '40px',
            backgroundColor: 'transparent',
            border: 'none',
            boxShadow: 'none',
            color: 'white',
            '&:hover': {
              border: 'none',
            },
          }),
          singleValue: (provided) => ({
            ...provided,
            color: 'white',
          }),
          input: (provided) => ({
            ...provided,
            color: 'white',
            backgroundColor: 'transparent',
          }),
          placeholder: (provided) => ({
            ...provided,
            color: 'white',
          }),
          menu: (provided) => ({
            ...provided,
            backgroundColor: '#333',
            boxShadow: 'none',
            border: 'none',
            marginTop: 0,
          }),
          menuList: (provided) => ({
            ...provided,
            padding: 0,
          }),
          option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isFocused ? '#444' : 'transparent',
            color: 'white',
            cursor: 'pointer',
          }),
          dropdownIndicator: (provided) => ({
            ...provided,
            color: 'white',
            padding: 4,
          }),
          indicatorSeparator: () => ({
            display: 'none',
          }),
        }}
      />
    </div>
  );
};

export default VirtualizedSelect;